import React, { useState } from 'react';
import { 
  MagnifyingGlassIcon, 
  PlusIcon, 
  EyeIcon, 
  PencilIcon, 
  TrashIcon,
  AcademicCapIcon,
  UserGroupIcon,
  ClockIcon,
  CalendarIcon
} from '@heroicons/react/24/outline';

interface Course {
  id: string;
  title: string;
  description: string;
  instructor: string;
  duration: string;
  enrolledStudents: number;
  maxStudents: number;
  status: 'active' | 'draft' | 'archived';
  startDate: string;
  endDate: string;
  category: string;
  level: 'Beginner' | 'Intermediate' | 'Advanced';
}

const CoursesPage: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [selectedLevel, setSelectedLevel] = useState<string>('all');

  // Mock data - replace with actual API calls
  const courses: Course[] = [
    {
      id: '1',
      title: 'React Development Fundamentals',
      description: 'Learn the basics of React development including components, hooks, and state management.',
      instructor: 'Dr. Jane Smith',
      duration: '8 weeks',
      enrolledStudents: 32,
      maxStudents: 50,
      status: 'active',
      startDate: '2024-01-15',
      endDate: '2024-03-15',
      category: 'Web Development',
      level: 'Beginner'
    },
    {
      id: '2',
      title: 'Advanced JavaScript Patterns',
      description: 'Master advanced JavaScript concepts including design patterns and performance optimization.',
      instructor: 'Prof. John Doe',
      duration: '12 weeks',
      enrolledStudents: 28,
      maxStudents: 40,
      status: 'active',
      startDate: '2024-02-01',
      endDate: '2024-04-30',
      category: 'Programming',
      level: 'Advanced'
    },
    {
      id: '3',
      title: 'TypeScript Masterclass',
      description: 'Comprehensive course covering TypeScript from basics to advanced enterprise patterns.',
      instructor: 'Dr. Sarah Wilson',
      duration: '10 weeks',
      enrolledStudents: 0,
      maxStudents: 35,
      status: 'draft',
      startDate: '2024-03-01',
      endDate: '2024-05-15',
      category: 'Programming',
      level: 'Intermediate'
    }
  ];

  const categories = ['Web Development', 'Programming', 'Data Science', 'Design'];

  const filteredCourses = courses.filter(course => {
    const matchesSearch = course.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         course.instructor.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         course.category.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'all' || course.category === selectedCategory;
    const matchesLevel = selectedLevel === 'all' || course.level === selectedLevel;
    return matchesSearch && matchesCategory && matchesLevel;
  });

  const getStatusBadge = (status: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full";
    switch (status) {
      case 'active':
        return `${baseClasses} bg-green-500/20 text-green-400 border border-green-500/30`;
      case 'draft':
        return `${baseClasses} bg-yellow-500/20 text-yellow-400 border border-yellow-500/30`;
      case 'archived':
        return `${baseClasses} bg-gray-500/20 text-gray-400 border border-gray-500/30`;
      default:
        return baseClasses;
    }
  };

  const getLevelBadge = (level: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full";
    switch (level) {
      case 'Beginner':
        return `${baseClasses} bg-blue-500/20 text-blue-400 border border-blue-500/30`;
      case 'Intermediate':
        return `${baseClasses} bg-orange-500/20 text-orange-400 border border-orange-500/30`;
      case 'Advanced':
        return `${baseClasses} bg-red-500/20 text-red-400 border border-red-500/30`;
      default:
        return baseClasses;
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Courses</h1>
          <p className="text-gray-300 mt-1">Manage course offerings and curriculum</p>
        </div>
        <button className="bg-gradient-to-r from-indigo-500 to-purple-500 hover:from-indigo-600 hover:to-purple-600 text-white px-4 py-2 rounded-lg flex items-center space-x-2 transition-all duration-200">
          <PlusIcon className="h-5 w-5" />
          <span>Add Course</span>
        </button>
      </div>

      {/* Filters and Search */}
      <div className="bg-white/10 backdrop-blur-md rounded-xl border border-white/20 p-6">
        <div className="flex flex-col lg:flex-row gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
            <input
              type="text"
              placeholder="Search courses..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-indigo-400 transition-colors duration-200"
            />
          </div>

          {/* Category Filter */}
          <select
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
            className="px-4 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:outline-none focus:border-indigo-400 transition-colors duration-200"
          >
            <option value="all">All Categories</option>
            {categories.map(category => (
              <option key={category} value={category}>{category}</option>
            ))}
          </select>

          {/* Level Filter */}
          <select
            value={selectedLevel}
            onChange={(e) => setSelectedLevel(e.target.value)}
            className="px-4 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:outline-none focus:border-indigo-400 transition-colors duration-200"
          >
            <option value="all">All Levels</option>
            <option value="Beginner">Beginner</option>
            <option value="Intermediate">Intermediate</option>
            <option value="Advanced">Advanced</option>
          </select>
        </div>
      </div>

      {/* Courses Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredCourses.map((course) => (
          <div key={course.id} className="bg-white/10 backdrop-blur-md rounded-xl border border-white/20 p-6 hover:bg-white/15 transition-all duration-300 group">
            {/* Course Header */}
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <div className="flex items-center space-x-2 mb-2">
                  <span className={getStatusBadge(course.status)}>
                    {course.status}
                  </span>
                  <span className={getLevelBadge(course.level)}>
                    {course.level}
                  </span>
                </div>
                <h3 className="text-lg font-semibold text-white group-hover:text-indigo-300 transition-colors duration-200">
                  {course.title}
                </h3>
              </div>
              <div className="flex space-x-2 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                <button className="text-indigo-400 hover:text-indigo-300 transition-colors duration-200">
                  <EyeIcon className="h-5 w-5" />
                </button>
                <button className="text-gray-400 hover:text-white transition-colors duration-200">
                  <PencilIcon className="h-5 w-5" />
                </button>
                <button className="text-red-400 hover:text-red-300 transition-colors duration-200">
                  <TrashIcon className="h-5 w-5" />
                </button>
              </div>
            </div>

            {/* Course Description */}
            <p className="text-gray-300 text-sm mb-4 line-clamp-3">
              {course.description}
            </p>

            {/* Course Info */}
            <div className="space-y-3">
              <div className="flex items-center text-sm text-gray-300">
                <AcademicCapIcon className="h-4 w-4 mr-2 text-indigo-400" />
                <span>Instructor: {course.instructor}</span>
              </div>

              <div className="flex items-center text-sm text-gray-300">
                <ClockIcon className="h-4 w-4 mr-2 text-indigo-400" />
                <span>Duration: {course.duration}</span>
              </div>

              <div className="flex items-center text-sm text-gray-300">
                <UserGroupIcon className="h-4 w-4 mr-2 text-indigo-400" />
                <span>Enrolled: {course.enrolledStudents}/{course.maxStudents}</span>
                <div className="flex-1 ml-2">
                  <div className="w-full bg-gray-700 rounded-full h-2">
                    <div 
                      className="bg-gradient-to-r from-indigo-500 to-purple-500 h-2 rounded-full"
                      style={{ width: `${(course.enrolledStudents / course.maxStudents) * 100}%` }}
                    />
                  </div>
                </div>
              </div>

              <div className="flex items-center text-sm text-gray-300">
                <CalendarIcon className="h-4 w-4 mr-2 text-indigo-400" />
                <span>
                  {new Date(course.startDate).toLocaleDateString()} - {new Date(course.endDate).toLocaleDateString()}
                </span>
              </div>
            </div>

            {/* Category Tag */}
            <div className="mt-4 pt-4 border-t border-white/10">
              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-500/20 text-indigo-400 border border-indigo-500/30">
                {course.category}
              </span>
            </div>
          </div>
        ))}
      </div>

      {filteredCourses.length === 0 && (
        <div className="bg-white/10 backdrop-blur-md rounded-xl border border-white/20 p-12 text-center">
          <AcademicCapIcon className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-white">No courses found</h3>
          <p className="mt-1 text-sm text-gray-400">
            {searchTerm ? 'Try adjusting your search criteria' : 'Get started by creating a new course'}
          </p>
        </div>
      )}
    </div>
  );
};

export default CoursesPage;
